$size-arrow: 10px;
$size-arrow-diag: 14px;
$arrow-color: $gray-tint-4;


.arrow-box {
  position: absolute;
  box-sizing: border-box;
  background: $arrow-color;

  &:after {
    content: "";
    position: absolute;
    width: 0;
    height: 0;

    box-sizing: border-box;
    border: $size-arrow solid black;

    transform-origin: 0 0;
    transform: rotate(-45deg);
    margin-left: -13.5px;
  }

  &.a-top {
    top: $size-arrow-diag;
    &:after {
      box-shadow: 1px -1px 1px 0 rgba(0, 0, 0, 0.1);
      border-color: $arrow-color $arrow-color transparent transparent;
    }
  }
  &.a-bottom {
    bottom: 1px;
    &:after {
      box-shadow: -3px 3px 3px 0 rgba(0, 0, 0, 0.4);
      border-color: transparent transparent $arrow-color $arrow-color;
    }
  }
  &.a-left{
    &:after {
      box-shadow: -2px -2px 3px 0 rgba(0, 0, 0, 0.2);
      border-color: $arrow-color transparent transparent $arrow-color;
    }
  }
  &.a-right{
    right: 1px;
    &:after {
      box-shadow: 2px 2px 2px 0 rgba(0, 0, 0, 0.3);
      border-color: transparent $arrow-color $arrow-color transparent;
    }
  }

  &.a-white {
    background: $white;
    &.a-top {
      &:after {
        border-color: $white $white transparent transparent;
      }
    }
    &.a-bottom {
      &:after {
        border-color: transparent transparent $white $white;
      }
    }
    &.a-left{
      &:after {
        border-color: $white transparent transparent $white;
      }
    }
    &.a-right{
      &:after {
        border-color: transparent $white $white transparent;
      }
    }
  }

  &.a-rounded {
    &.a-top {
      &:after {
        border-top-right-radius: 4px;
      }
    }
    &.a-bottom {
      &:after {
        border-bottom-left-radius: 4px;
      }
    }
    &.a-left{
      &:after {
        border-top-left-radius: 4px;
      }
    }
    &.a-right{
      &:after {
        border-bottom-right-radius: 4px;
      }
    }
  }
}