@import '../styles/variables';
@import '../styles/mixins';
@import '../styles/arrow';
@import '../styles/buttons';
@import '../styles/ping';
// In the future we must separate all the Tour specific items into separate files
@import "./FamilyOnboarding/familyOnboardingTour";

$width-default: 430px;

//@include border-radius($border-radius-base);

.intro-tour-dialog {
  width: $width-default;
  margin: inherit;
  background-color: $white;

  .dialogContent {
    padding: 0;
    overflow: hidden;
    position: relative;

    #contentText1,
    #contentText2 {
      padding-right: 12px;
    }
  }

  &.tour-welcome-step {
    box-shadow: none;
    bottom: initial;
    z-index: 3;

    .intro-tour {
      width: calc(100% - 70px);
      padding-left: 50px;
    }
  }
}

.intro-tour {
  margin-top: 15px;
  margin-bottom: 15px;
  width: $width-default - 30px;
  color: $gray-shade-5;

  @include font-sm();

  h1 {
    @include font-xm();
    margin-bottom: $margin-sm;
    margin-top: $margin-sm;
  }

  .main-icon {
    width: 75px;
    height: 75px;
  }

  .btn-close {
    width: 12px;
    height: 12px;
    cursor: pointer;
  }
  .buttons {
    padding-left: 0px;
  }
}

.tour-overlay {
  background-color: rgba($gray-shade-5, .3);

  .tour {
    opacity: .3;
  }
}

.intro-tour-dialog.omar-migration-override {
  border-radius: 10px;
}

.tour-overrides {
  .middlePosition {
    top: 235px;
  }
  #tools, #ad {
    &.middlePosition {
      top: 60px;
    }
  }
  #main {
    top: 295px;
  }
  .bottom {
    top: 295px;
  }
  .emptyVault {
    top: 160px;
  }

  #emptySites,
  #emptyNotes,
  #emptyFormFills,
  #emptySentShares,
  #emptySharedFolders,
  #emptyReceivedShares,
  #emptyEmergencyTrusted,
  #emptyEmergencyTrusting,
  #emptyCreditMonitoring
  {
    .emptyVault {
      top: 0;

      .tour {
        margin-top: 0;
        top: 0;
      }
    }
  }
}

.invisible-shade {
  opacity: 0;
  z-index: 7;
  position: absolute;
  width: 100%;
  height: 100%;
}
.dark-shade {
  z-index: 2;
  background-color: rgba(52, 61, 68, 0.6);
  position: absolute;
  top: 60px;
  bottom: 0;
  left: 60px;
  right: 0;
}

.omar-migration-header-image {
  display: none;
}

.read-more {
  display: none;
}

.omar-migration-override {
  &.migration-first-step {
    border-radius: 4px;
    width: 430px;
    height: 384px;

    p {
      color: #000000;
    }

    .btn-primary {
      width: 180px;
      font-size: 11px;
    }

    .btn-base {
      font-size: 11px;
    }

    #contentText1 {
      margin-bottom: 12px;
      font-size: 14px;
    }

    .btn-close {
      display: none;
    }

    .intro-tour {
      padding: 24px 24px 0;
      margin: 0;
    }

    .omar-migration-header-image {
      display: block;
    }

    #mainTitle {
      display: block;
      font-size: 18px;
      color: #646d73;
      line-height: 1;
      margin: 0 0 18px;
      width: 100%;
      font-weight: 600;
    }
  }
  &.migration-second-step {
    max-height: 160px!important;
    height: 160px;

    #contentText1 {
      margin-bottom: 12px;
      font-size: 14px;
    }

    #contentText2 {
      display: inline-block!important;
      padding-right: 0;
      font-size: 14px;
    }

    .read-more {
      display: inline-block;
      color: #3598db;
      text-decoration: none;
      padding-left: 6px;
    }

    #btnClose {
      display: none;
    }

    p {
      color: #000000;
    }

    .btn-primary {
      font-size: 11px;
      width: 120px;
    }

    .buttons {
      margin-top: 12px;
      padding: 0 12px 0 0;
    }
  }
  &.migration-last-step {
    .btn-primary {
      min-width: 120px;
    }
  }

  .btn-base {
    background-color: #ffffff;
    border: 1px solid #e7e8ea;
    border-radius: 50px;
    box-shadow: 0 1px 1px rgba(59, 74, 84, 0.1);
    color: #646d73;
    display: inline-block;
    font-weight: 600;
    font-size: 12px;
    letter-spacing: -0.3px;
    line-height: 1.6;
    height: 36px;
    margin: 6px;
    padding: 6px 24px;
    outline: 0;
    text-align: center;
    text-transform: uppercase;
    text-decoration: none;
    cursor: pointer;
    -webkit-transition: box-shadow 225ms cubic-bezier(0.25, 0.8, 0.25, 1), border-color 225ms cubic-bezier(0.25, 0.8, 0.25, 1), background-color 225ms cubic-bezier(0.25, 0.8, 0.25, 1);
    -moz-transition: box-shadow 225ms cubic-bezier(0.25, 0.8, 0.25, 1), border-color 225ms cubic-bezier(0.25, 0.8, 0.25, 1), background-color 225ms cubic-bezier(0.25, 0.8, 0.25, 1);
    -ms-transition: box-shadow 225ms cubic-bezier(0.25, 0.8, 0.25, 1), border-color 225ms cubic-bezier(0.25, 0.8, 0.25, 1), background-color 225ms cubic-bezier(0.25, 0.8, 0.25, 1);
    -o-transition: box-shadow 225ms cubic-bezier(0.25, 0.8, 0.25, 1), border-color 225ms cubic-bezier(0.25, 0.8, 0.25, 1), background-color 225ms cubic-bezier(0.25, 0.8, 0.25, 1);
    transition: box-shadow 225ms cubic-bezier(0.25, 0.8, 0.25, 1), border-color 225ms cubic-bezier(0.25, 0.8, 0.25, 1), background-color 225ms cubic-bezier(0.25, 0.8, 0.25, 1);
  }

  .btn-primary {
    color: #ffffff;
    background-color: #d22d27;
    border-color: #d22d27;
    box-shadow: 0 1px 1px rgba(59, 74, 84, 0.1), 0 1px 6px rgba(59, 74, 84, 0.2);
  }

  .btn-link {
    color: #646d73;
    font-weight: 600;
    border-color: transparent;
    background-color: transparent;
    box-shadow: none;
  }

  .buttons {
    float: right;
    margin-top: 20px;
    width: 100%;
    padding-left: 95px;
  }
}

.newFlag {
  position: absolute;
  right: 10px;
  top: 8px;
  font-size: 12px;
  color: #a1a9ad;
  background-color: #22303a;
  padding: 6px 7px;
  border-radius: 25px;
  line-height: 1;
  text-transform: uppercase;
}
.leftMenuItem {
  &.newFlag.active {
    color: #22303a;
    background-color: #ffffff;
  }
}

.newFlag.active {
  color: #22303a;
  background-color: #ffffff;
}

