$dotSize: 10px;

.ping-wrap {
  z-index: 7;
  position: absolute;
  top: 0;
  left: 0;
  padding: 0;
  margin: 0;
}

.ping-container {
  float: left;
  display: block;
  margin: -1 * $dotSize / 2;
  position: absolute;

  .ring {
    display: block;
    float: left;
    position: absolute;
    border-radius: 70px;
    padding: 40px;

    @include transform(scale(.1));
    -webkit-transform-origin: center;

    &.ping {
      top: -45px;
      left: -45px;
      border: solid $blue-tint-3 10px;

      @include animate(ping-kf, 2s, infinite, normal, ease-in);
    }
    &.ping-2 {
      @extend .ping;
      -webkit-animation-delay: 1s;
      animation-delay: 1s;
    }
  }

  .dot {
    width: $dotSize;
    height: $dotSize;
    position: absolute;
    text-decoration: none;
    background-color: $blue;
    border-radius: $dotSize / 2;
    box-shadow: 0px 0px 20px 4px $blue-tint-3;

    &.breathe {
      @include animate(breathe, 2s, infinite, alternate);
    }
  }

  @-webkit-keyframes ping-kf {
    from {
      @include transform(scale(.1));
      opacity: 1;
    }
    to {
      @include transform(scale(.5));
      opacity: .1;
    }
  }

  @-webkit-keyframes breathe {
    from {
      border-color: $blue;
      background-color: $blue;
    }
    to {
      border-color: $blue-tint-3;
      background-color: $blue-tint-3;
    }
  }
}